    padding: 15px 30px;
    position: absolute;
    top: -15px;
    -webkit-transition: opacity ease-in-out 0.3s;
    -o-transition: opacity ease-in-out 0.3s;
    transition: opacity ease-in-out 0.3s;
    visibility: hidden;
    white-space: nowrap;
    width: 70px; }
  .tooltip:after {
    border: solid 7px transparent;
    border-right-color: #FFF;
    content: "";
    left: 15px;
    opacity: 0;
    position: absolute;
    top: 2px;
    -webkit-transition: opacity ease-in-out 0.3s;
    -o-transition: opacity ease-in-out 0.3s;
    transition: opacity ease-in-out 0.3s;
    visibility: hidden; }
  .tooltip:hover {
    cursor: pointer; }
    .tooltip:hover .tip {
      display: inline;
      opacity: 1;
      visibility: initial; }
    .tooltip:hover:after {
      display: block;
      opacity: 1;
